function sliderMain() {
    var swiper = new Swiper('.main .swiper', {
        effect: "cards",
        grabCursor: true,
        centeredSlides: true,
        loop: true,
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        },
    })
}

function sliderDoctors() {
    var swiper = new Swiper('.doctors .swiper', {
        slidesPerView: '4',
        spaceBetween: 24,
        pagination: {
            el: ".doctors .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1.5,
                spaceBetween: 16,
            },
            577: {
                slidesPerView: 2,
                spaceBetween: 16,
            },
            769: {
                slidesPerView: 3,
                spaceBetween: 16,
            },
            1201: {
                slidesPerView: 4,
                spaceBetween: 24,
            },

        }
    })
}

function sliderWorks() {
    var swiper = new Swiper('.works .swiper', {
        slidesPerView: '2',
        spaceBetween: 24,
        pagination: {
            el: ".works .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                spaceBetween: 16,
            },
            769: {
                slidesPerView: 2,
                spaceBetween: 16,
            },
            993: {
                spaceBetween: 24,
            },

        }
    })
}

function sliderCooperation() {
    var swiper = new Swiper('.cooperation .swiper', {
        slidesPerView: '2',
        spaceBetween: 24,
        pagination: {
            el: ".cooperation .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            768: {
                slidesPerView: 2,
            },
        }
    })
}

function sliderNews() {
    var swiper = new Swiper('.news .swiper', {
        slidesPerView: '4',
        spaceBetween: 24,
        pagination: {
            el: ".news .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            577: {
                slidesPerView: 2,
            },
            993: {
                slidesPerView: 3,
            },
            1201: {
                slidesPerView: 4,
            },

        }
    })
}

function sliderDoctorsMain() {
    var swiper = new Swiper('.doctors-main .swiper', {
        slidesPerView: '4',
        spaceBetween: 24,
        grid: {
            rows: 2,
        },
        pagination: {
            el: ".doctors-main .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1.5,
                spaceBetween: 16,
                grid: {
                    rows: 1,
                },
            },
            577: {
                slidesPerView: 2,
                spaceBetween: 16,
                grid: {
                    rows: 1,
                },
            },
            769: {
                slidesPerView: 3,
                spaceBetween: 16,
                grid: {
                    rows: 1,
                },
            },
            1201: {
                slidesPerView: 4,
                spaceBetween: 24,
                grid: {
                    rows: 2,
                },
            },

        }
    })
}

$(document).ready(function() {

    sliderMain();
    sliderDoctors();
    sliderWorks();
    sliderCooperation();
    sliderNews();
    sliderDoctorsMain();



    // Menu
    $('.header__menu').click(function () {
        $('.burger').toggleClass('burger--active')
        $('.header__right').toggleClass('header__right--active')
        $('body').toggleClass('fixed-body')
    })

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs").find(".tabs__content").removeClass("tabs__content--active")
        $(this).parents(".tabs").find(`.tabs__content[data-content-path="${path}"]`).addClass("tabs__content--active")
    })

    // Accordion
    $(".accordion-show").click((function(){
        $(this).toggleClass("accordion-show--active"),
        $(this).siblings(".accordion-hidden").slideToggle()}));


})